.App {
  font-family: sans-serif;
  text-align: center;
}
html {
  background: url(summer.jpg) no-repeat center center;

  background-size: cover;
}
.outer-div {
  padding: 20px;
  text-align: center;
}
.inner-div {
  display: inline-flexbox;

  padding: 80px;
}
h2 {
  color: rgb(223, 11, 134);
  font-family: ‘Helvetica Neue’, sans-serif;
  font-size: 30px;
  font-weight: 50;
  line-height: 25px;
  letter-spacing: 2px;
  padding: 0 0 40px;
}
.left-div {
  display: inline-block;
  max-width: 300px;
  vertical-align: top;
}
.right-div {
  display: inline-block;
  max-width: 150px;
}
img {
  width: 15%;
  height: 20%;
}
